.search {
  padding: 30px 40px;
  margin: auto;
}

.resultsText {
  font-weight: 600;
  font-size: 16px;
  margin-bottom: 15px;
}

:global(.dark) .resultsText {
  color: var(--white);
}

@media screen and (min-width: 1536px) {
  .search {
    padding: 50px 75px;
    max-width: 1500px;
    margin: auto;
  }
}